export default ":host {\n  margin-right: 16px;\n}\n:host tdw-icon.shake {\n  animation: shake 1s infinite ease both;\n  transform: translate3d(0, 0, 0);\n  backface-visibility: hidden;\n  perspective: 1000px;\n}\n@keyframes shake {\n  10%,\n  30%,\n  50%,\n  70%,\n  90% {\n    transform: translate3d(-2px, 0, 0);\n  }\n  10%,\n  25%,\n  50%,\n  60%,\n  90% {\n    transform: rotate(5deg);\n  }\n  20%,\n  40%,\n  55%,\n  80%,\n  100% {\n    transform: rotate(-5deg);\n  }\n  20%,\n  40%,\n  60%,\n  80%,\n  100% {\n    transform: rotate(-5deg) translate3d(2px, 0, 0);\n  }\n}\n:host tdw-icon {\n  cursor: pointer;\n  height: 24px;\n  width: 24px;\n  line-height: 24px;\n}\n";